import Vue from 'vue'
import Router from 'vue-router'

import FullscreenLayout from './../components/Dashboard/Layout/FullscreenLayout.vue'

import ArtistsDirectory from '@/components/Dashboard/Views/Artists/Directory'
import ArtistProfile from '@/components/Dashboard/Views/Artists/ArtistProfile'

import ArtworksMap from '@/components/Dashboard/Views/Artworks/ArtworksMap'
import ArtworkDetail from '@/components/Dashboard/Views/Artworks/ArtworkDetail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: FullscreenLayout,
      redirect: '/artists',
      children: [
        {
          path: 'artists',
          name: 'ArtistsDirectory',
          component: ArtistsDirectory
        },
        {
          path: 'artists/:id',
          name: 'ArtistProfile',
          component: ArtistProfile
        },
        {
          path: 'artworks',
          name: 'ArtworksMap',
          component: ArtworksMap
        },
        {
          path: 'artworks/:id',
          name: 'ArtworkDetail',
          component: ArtworkDetail
        }
      ]
    }
  ],
  // mode: 'history',
  scrollBehavior (to, from, savedPosition) {
    console.log('scrollBehavior', savedPosition)
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})



