<template>
  <v-container class="vivid-arts-directory">
    <div class="vivid-arts-artwork-map">
      <v-flex xs12>
        <gmap-map
          class="vap-artworks-map"
          ref="artworksMap"
          :center="center"
          :zoom="7"
          style="width:100%;height:80vh;"
        >


          <!-- <google-cluster> -->
            <gmap-marker
              :key="i"
              v-for="(m, i) in markers"
              :position="m.position"
              :icon="m.icon"
              :clickable="true"
              @click="toggleInfoWindow(m,i)"
            ></gmap-marker>
          <!-- </google-cluster> -->
        </gmap-map>
      </v-flex>

      <v-container>
        <!-- <pulse-loader v-if="!directory" class="va-page-loader" :color="color" :size="size" :margin="margin" :radius="radius"></pulse-loader> -->
        <v-layout row wrap>

          <v-flex xs12 class="artwork-search">
            <v-container fluid grid-list-md>
              <h1 class="display-4 page-title mb-3 text-xs-center" v-if="$route.params.region">{{$route.params.region}}</h1>
              <v-layout row wrap>

                <v-flex xs12 sm12 md4>
                  <v-layout row wrap>
                    <v-flex xs10>
                      <v-text-field
                        name="input-1-3"
                        v-model="artworkSearch"
                        class="search-textfield"
                        label="Search Artwork"
                        single-line
                        prepend-icon="search"
                      ></v-text-field>
                    </v-flex>
                    <v-flex xs2>
                      <!-- <a v-show="artworkSearch" href class="btn flat" @click.prevent="artworkSearch = ''">Clear</a> -->
                      <v-btn
                        v-show="artworkSearch" outline large color="indigo"
                        @click.prevent="artworkSearch = ''">
                        <v-icon>fa-times-circle</v-icon>
                      </v-btn>
                    </v-flex>
                  </v-layout>
                </v-flex>

                <v-flex xs12 sm12 md4>
                  <p class="headline text-xs-center">{{filteredArtworks.length}} Artworks</p>
                </v-flex>

                <v-flex xs12 sm12 md4 v-if="cities.length">
                  <span v-show="false">{{viewableArtworks.length}}/{{fRegions.length}}</span>
                  <p>Region</p>
                  <v-select v-bind:items="cities" v-model="dd1" overflow label="Select" class="capitalize"></v-select>
                </v-flex>

                <v-flex xs12 sm12 md4 v-if="activeCategories.length">
                  <p>Type</p>
                  <v-select v-bind:items="activeCategories" v-model="dd2" overflow item-value="name" item-text="name" label="Select"></v-select>
                </v-flex>

                <v-flex xs12 sm12 md4 v-if="viewableArtworks.length" class="text-xs-right">
                  <v-flex xs12>
                    <v-pagination :length="numberOfPages" v-model="page" circle></v-pagination>
                  </v-flex>
                </v-flex>
                <!-- <v-flex sm12 sm4 v-if="false">
                  <p>Sub-Type</p>
                  <v-select v-bind:items="activeCategories" v-model="dd3" overflow item-value="name" item-text="name" label="Select"></v-select>
                </v-flex> -->
              </v-layout>
            </v-container>
          </v-flex>

          <v-progress-linear v-if="!viewableArtworks.length" :indeterminate="true"></v-progress-linear>

          <v-flex xs12 id="artwork-grid-wrap" ref="artworkGridWrap">
            <v-container fluid v-bind="{ [`grid-list-md`]: true }">
              <v-layout row wrap>
                <v-flex
                  xs12 sm6
                  :class="itemClassSize"
                  :key="artwork.permalink"
                  v-for="artwork in viewableArtworks"
                  v-if="viewableArtworks.length > 0"
                  class="pa-4">
                  <v-card flat tile>
                    <router-link :to="{ name: 'ArtworkDetail', params: {id: artwork._id, region: (artwork._regionId ? artwork._regionId : 'all')} }">
                      <v-card-media class="profile-image"
                        :src="artwork.mainImageURL | thumbnailURL"
                        height="200px"
                      >
                      </v-card-media>
                    </router-link>
                    <v-card-title primary-title>
                      <div>
                        <router-link class="artwork-name" :to="{ name: 'ArtworkDetail', params: { id: artwork._id, region: (artwork._regionId ? artwork._regionId : 'all')} }">
                          <h3 class="headline mb-0">{{ artwork.title }}</h3>
                        </router-link>
                        <div class="artwork-location">
                          {{ artwork.address.street }}<br/>
                          {{ artwork.address.city }} {{ artwork.address.state }}, {{ artwork.address.zip }}
                        </div>
                      </div>
                    </v-card-title>
                    <v-card-actions class="pb-3">
                      <router-link class="white--text btn px-3" tag="button" :style="{backgroundColor: themeColor}" :to="{ name: 'ArtworkDetail', params: { id: artwork._id, region: (artwork._regionId ? artwork._regionId : 'all')} }">View Artwork</router-link>
                      <!-- <router-link class="indigo--text btn btn--flat" tag="button" :to="{ name: 'EditArtwork', params: { id: artwork.permalink} }">Edit</router-link> -->
                    </v-card-actions>
                  </v-card>
                </v-flex>
                <v-flex v-if="viewableArtworks.length === 0 && pageLoader" xs12>
                  <h2 class="headline text-xs-center">{{errorMsg}}</h2>
                </v-flex>
                <v-flex xs12 class="text-xs-center" v-if="viewableArtworks.length">
                  <v-pagination :length="numberOfPages" v-model="page" circle></v-pagination>
                </v-flex>
              </v-layout>
            </v-container>

          </v-flex>
        </v-layout>
      </v-container>
    </div>
  </v-container>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'
import Vue from 'vue'
import VueScrollTo from 'vue-scrollto'
import * as VueGoogleMaps from 'vue2-google-maps'
// import GoogleCluster from 'google-cluster'

Vue.use(VueGoogleMaps, {
  load: {
    key: 'AIzaSyDosNGj0m6Fl7t35UzMDIOS-Thmtshgf9s',
    libraries: 'places' // This is required if you use the Autocomplete plugin
    // OR: libraries: 'places,drawing'
    // OR: libraries: 'places,drawing,visualization'
    // (as you require)
  }
})

export default {
  name: 'ArtworkMap',
  components: {
    swiper,
    swiperSlide,
    // GoogleCluster,
    PulseLoader
  },
  created () {
    this.fetchArtworks()
    this.fetchEnterpriseDirectory()
  },
  data () {
    return {
      artworkSearch: null,
      bounds: {},
      infoContent: '',
      infoImage: '',
      infoWindowPos: {
        lat: 0,
        lng: 0
      },
      infoWinOpen: false,
      center: {lat: 26.7419207, lng: -80.1990802},
      markers: [],

      pageLoader: false,
      radius: 40,
      color: '#136FAB',
      size: '30px',

      scrollOptions: {
        el: '#artwork-grid-wrap',
        easing: 'ease-in',
        offset: -200,
        cancelable: true,
        onDone: function () {
          // scrolling is done
        },
        onCancel: function () {
          // scrolling has been interrupted
        },
        x: false,
        y: true
      },
      page: 1,
      maxArtworkCount: 24,
      numberOfPages: 0,
      itemClassSize: 'md4',
      dd1: null,
      dd2: null,
      dd3: null,
      artworkTypes: window.VividArts.types || [],
      cities: [],
      // enterprise: {},
      fRegions: [],

      activeCategories: [],
      directory: {},
      directoryType: 'artworks',
      errorMsg: 'No results found.',

      swiperOption: {
        pagination: '.swiper-pagination',
        nextButton: '.swiper-button-next',
        prevButton: '.swiper-button-prev',
        paginationClickable: true,
        spaceBetween: 30,
        loop: true,
        autoplay: 2500,
        effect: 'fade'
      },

      originalActiveArtworks: [],
      filteredArtworks: [],
      viewableArtworks: [],
      dropdown_regions: []
    }
  },
  watch: {
    enterprise (newVal, oldVal) {
      if (newVal !== oldVal) {
        this.fetchArtworks()
        this.fetchEnterpriseDirectory()
      }
    },
    page (newVal, oldVal) {
      if (newVal !== oldVal) {
        VueScrollTo.scrollTo('#artist-grid-wrap', 500, this.scrollOptions)
        this.filterArtworksByPage(this.filteredArtworks)
      }
    },
    artworkSearch (newVal, oldVal) {
      if (newVal !== oldVal) {
        if (newVal === '') {
          this.filterArtworksByPage(this.filteredArtworks)
          return
        }
        console.log('artistSearch', newVal)
        var array = this.artworkSearchHandler(newVal.toLowerCase(), this.originalActiveArtworks, this.filteredArtworks)
        if (typeof array !== 'undefined') {
          this.filterArtworksByPage(array)
          // console.log('artistSearch', newVal, array)
        }
      }
    }
  },
  computed: {
    enterprise () {
      return this.$store.getters.getEnterprise
    },
    themeColor () {
      return (this.$store.state.config && this.$store.state.config.themeColor) || '#9E9E9E'
    }
  },
  methods: {
    fetchArtworks () {
      // console.log('fetch artworks')
      if (this.enterprise === null || typeof this.enterprise.enterpriseId === 'undefined') {
        return
      }

      let region = (this.$route.params.region) ? this.$route.params.region : ''
      var url = this.$store.state.apiURL + `/v2/enterprise/${this.enterprise.enterpriseId}/artworks/${region}`
      let vm = this
      vm.pageLoader = true
      this.axios.get(url, this.$store.state.axiosConfig).then((response) => {
        // var keys = Object.keys(response.data.artworks)
        var data = Object.values(response.data.artworks)
        .map(function (item, idx) {
          // item.permalink = keys[idx]
          return item
        })
        // console.log('API | artworks', data)

        var artworks = data.filter(function (artwork) {
          return artwork.status === 'active' // && (artwork.showOnMap === true)
        })
        vm.markers = Vue._.clone(artworks)
        .filter(function (item) {
          return (typeof item.location !== 'undefined' && typeof item.location.coordinates !== 'undefined' && item.location.coordinates.length === 2 && item.location.coordinates !== [0, 0])
        })
        .map(function (item) {
          return {
            position: {lat: item.location.coordinates[0], lng: item.location.coordinates[1]},
            icon: 'http://res.cloudinary.com/vividarts/image/upload/c_scale,w_30/v1506999888/poi-marker_hw5lin.png',
            infoText: item.title,
            infoImage: item.mainImageURL
          }
        })

        vm.filterArtworksByPage(artworks)
        vm.filteredArtworks = artworks
        vm.originalActiveArtworks = artworks
        vm.bounds = new window.google.maps.LatLngBounds()
        var bounds = vm.bounds
        for (var i = 0; i < vm.markers.length; i++) {
          let artwork = vm.markers[i]
          bounds.extend(artwork.position)
        }
        vm.fitArtworkMarkers()
        vm.pageLoader = false
        console.log('Artworks', artworks)
      })
    },
    fitArtworkMarkers () {
      console.log('Setup map', this.$refs)
      this.$refs.artworksMap.$mapObject.fitBounds(this.bounds)
    },
    toggleInfoWindow (marker, idx) {
      this.infoWindowPos = marker.position
      this.infoContent = marker.infoText
      this.infoImage = marker.infoImage

      // check if its the same marker that was selected if yes toggle
      if (this.currentMidx === idx) {
        this.infoWinOpen = !this.infoWinOpen
      } else {
        // if different marker set infowindow to open and reset current marker index
        this.infoWinOpen = true
        this.currentMidx = idx
      }
    },
    filterArtworksByPage (artists) {
      console.log('filterArtworksByPage')
      let mac = this.maxArtworkCount
      let al = artists.length
      let start = (this.page - 1) * mac
      this.numberOfPages = Math.ceil(al / mac)
      let rtn = Vue._.clone(artists, true).splice(start, mac)
      // debugger
      // console.log('numberOfPages', al, mac, this.numberOfPages, rtn)
      this.viewableArtworks = rtn
    },
    fetchEnterpriseDirectory () {
      // console.log('fetch enterprise directory', this.$store.state, this.$store.state.apiURL)
      if (this.enterprise === null || typeof this.enterprise.enterpriseId === 'undefined') {
        return
      }
      var url = this.$store.state.apiURL + `/v2/enterprise/${this.enterprise.enterpriseId}/directory/artworks`
      // var payload = { }
      var vm = this
      this.axios.get(url, this.$store.state.axiosConfig).then((response) => {
        var data = response.data.directory[0]
        console.log('fetchEnterpriseDirectory', data)

        if (typeof data === 'undefined' || data === null || data.length === 0) { return }

        vm.directory = data
        // console.log('directoryType ->')
        if (vm.directoryType === 'artworks') {
          vm.activeCategories = vm.directory.categories
          if (typeof vm.activeCategories !== 'undefined' && vm.activeCategories.length > 0) {
            vm.activeCategories.unshift({name: 'All'})
          } else {
            vm.activeCategories = [{name: 'All'}]
          }
        }

        if (typeof vm.directory.itemsPerRow !== 'undefined') {
          switch (parseInt(vm.directory.itemsPerRow)) {
            case 1:
              vm.itemClassSize = 'md12'
              break
            case 2:
              vm.itemClassSize = 'md6'
              break
            case 3:
              vm.itemClassSize = 'md4'
              break
            case 4:
              vm.itemClassSize = 'md3'
              break
            default:
              vm.itemClassSize = 'md4'
          }
        }
        // console.log('active directory', vm.directory)
        // this.form = this.initFormWithData(this.artwork)
      })
    },
    artworkSearchHandler (str, arr, arr2) {
      // var str = this.artistSearch
      // console.log('Searching for ', str, arr, arr2)
      if (typeof arr === 'undefined') {
        return
      }
      return arr.filter(function (item) {
        // console.log(item.fullName.toLowerCase(), str, (item.fullName.toLowerCase().indexOf(str) >= 0))
        if (item.title && item.title.toLowerCase().indexOf(str) >= 0) {
          return true
        }
        if (item.city && item.city.toLowerCase().indexOf(str) >= 0) {
          return true
        }
        if (item.permalink && item.permalink.toLowerCase().indexOf(str) >= 0) {
          return true
        }
        if (item._artistId && item._artistId.toLowerCase().indexOf(str) >= 0) {
          return true
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.vap-artworks-map{
  width: 100%;
  height: 500px;
}
.artwork-search .page-title {
  text-transform: uppercase;
}
</style>


