<template>
  <v-container class="artwork-profile-content">
    <div class="vivid-arts-artwork-detail" v-if="$store.getters.getEnterprise">

      <!-- <div class="vap-admin-toolbar" v-if="$store.getters.getUser && ['admin', 'enterprise'].indexOf($store.getters.getUser.type) >= 0 && artwork">
        <router-link class="right btn mx-3 px-3" :to="{ name: 'EditArtwork', params: {id: artwork._id} }">Edit Artwork</router-link>
      </div> -->

      <v-flex xs12 v-if="artwork && artwork.gallery">
        <v-carousel>
          <v-carousel-item v-for="(item,i) in artwork.gallery" :src="item.src" :key="i"></v-carousel-item>
        </v-carousel>
      </v-flex>

      <v-container class="mt-3 mb-5" v-if="artwork">
        <v-layout row wrap>

          <v-flex xs12>
            <v-breadcrumbs>
              <v-icon slot="divider">chevron_right</v-icon>
              <v-breadcrumbs-item
                v-for="item in breadcrumbs"
                :key="item.text"
                :disabled="item.disabled"
                :href="item.to"
              >
                {{ item.text }}
              </v-breadcrumbs-item>
            </v-breadcrumbs>
          </v-flex>

          <v-flex xs12 md8 v-if="artwork.content" class="pr-5">
            <span class="headline">About</span>
            <div v-if="artwork.content.html" class="title artwork-about" v-html="artwork.content.html"></div>
            <div v-else class="about text-content empty">
              <p></p>
              <p></p>
              <p></p>
            </div>
          </v-flex>

          <v-flex xs12 md4 v-if="artwork.content">
            <span class="headline">Location</span>
            <gmap-map
              v-if="mapReady"
              :center="mapCenter"
              :zoom="15"
              map-type-id="terrain"
              style="width: 100%; height: 140px"
            >
              <gmap-marker
                :key="index"
                v-for="(m, index) in mapMarkers"
                :position="m.position"
                :clickable="true"
                :draggable="true"
                @click="center=m.position"
              ></gmap-marker>
            </gmap-map>
            <span class="map-address" v-if="artwork.address">
              {{artwork.address.street}}<br/>
              {{artwork.address.city}} {{artwork.address.state}}, {{artwork.address.zip}}
            </span>
          </v-flex>

        </v-layout>
      </v-container>

      <div class="vap-artists-wrap" v-if="artwork">
        <v-parallax v-if="artwork.mainImageURL" :src="artwork.mainImageURL"></v-parallax>
        <div class="vap-artists-content" v-if="artists.length">
          <h3 class="display-3 white--text">Creating Artist</h3>
          <ul class="vap-artists-list" v-if="artists.length">
            <li v-for="artist in artists">
              <router-link :to="{ name: 'ArtistProfile', params: {id: artist._id} }">
                <div class="artist-image" :style="{ 'background-image': 'url(' + (artist.profileURL ? artist.profileURL : $store.state.placeholderArtistImg) + ')' }"></div>
                <div class="detail-wrap">
                  <div class="bubble">
                    <span class="detail-name display-1">
                      <span v-if="artist.artisticName">{{artist.artisticName}}</span>
                      <span v-else>{{artist.firstName}}<br/>{{artist.lastName}}</span>
                    </span>
                  </div>
                </div>
              </router-link>
            </li>
          </ul>
        </div>
      </div>

      <v-container v-if="artwork">
        <v-layout row wrap>

          <v-flex xs12 class="my-5">
            <h3 class="title">Nearby Artworks</h3>
          </v-flex>

          <v-flex xs12 md4 v-if="artwork.content" v-for="artwork in nearbyArtworks">

            <v-card class="ma-3">
              <router-link :to="{ name: 'ArtworkDetail', params: {id: artwork._id} }">
                <v-card-media :src="artwork.mainImageURL" height="200px"></v-card-media>
              </router-link>
              <v-card-title primary-title>
                <div>
                  <h3 class="headline mb-0">{{artwork.title}}</h3>
                  <h4 class="subtitle">{{artwork._distance ? `${artwork._distance} miles away` : 'Same Location'}}</h4>
                  <div>{{artwork.content.plain | truncate(150)}}</div>
                </div>
              </v-card-title>
              <v-card-actions>
                <router-link tag="button" class="btn white--text px-3 ml-3 flat" :style="{backgroundColor: themeColor}" :to="{ name: 'ArtworkDetail', params: {id: artwork._id} }">View Artwork</router-link>
                <!-- <v-btn flat color="orange">Share</v-btn> -->
              </v-card-actions>
            </v-card>
          </v-flex>

        </v-layout>
      </v-container>
    </div>
  </v-container>
</template>

<script>
import Vue from 'vue'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
// import * as VueGoogleMaps from 'vue2-google-maps'

export default {
  name: 'ArtistDetail',
  components: {
    swiper,
    swiperSlide
  },
  created () {
    this.fetchArtwork()
  },
  data () {
    return {
      pageLoader: false,
      artwork: {},
      artists: [],
      mapMarkers: [],
      nearbyArtworks: [],
      breadcrumbs: []
    }
  },
  watch: {
    '$route.params.id': function (id) {
      this.fetchArtwork()
    }
  },
  computed: {
    enterprise () {
      return this.$store.getters.getEnterprise
    },
    themeColor () {
      return (this.$store.state.config && this.$store.state.config.themeColor) || '#9E9E9E'
    },
    mapReady () {
      if (this.artwork.location && this.artwork.location.coordinates && this.artwork.location.coordinates.length === 2) {
        this.mapCenter = {lat: this.artwork.location.coordinates[0], lng: this.artwork.location.coordinates[1]}
        this.mapMarkers = [{ position: this.mapCenter }]
        return true
      }
      return false
    }
  },
  methods: {
    resetPage () {
      this.artwork = {}
      this.artists = []
      this.mapMarkers = []
      this.nearbyArtworks = []
      this.breadcrumbs = []
    },
    fetchArtwork () {
      let vm = this
      if (!this.$store.getters.getEnterprise) {
        setTimeout(() => {
          this.fetchArtwork()
        }, 300)
        return
      }
      console.log('fetch artwork')
      this.resetPage()
      this.pageLoader = true
      var requestURL = `${this.$store.state.apiURL}/v2/enterprise/${this.$store.getters.getEnterprise.enterpriseId}/artworks/${this.artwork._regionId ? this.artwork._regionId : 'all'}/${this.$route.params.id}`
      this.axios.get(requestURL, this.$store.state.axiosConfig).then((response) => {
        // debugger
        if (!response.data.success) {
          this.$notify({
            group: 'main',
            title: 'Failed to get artwork',
            text: 'Invalid request.'
          })
          return
        }

        var data = response.data.artwork
        if (typeof data === 'undefined' || data === null || data.length === 0) { return }

        vm.artwork = Vue._.clone(data, false)
        if (vm.artwork._artistIds && vm.artwork._artistIds.length) {
          vm.fetchArtists()
        }
        if (vm.artwork.location && vm.artwork.location.coordinates.length === 2) {
          this.fetchNearbyArtworks()
        }
        this.setupBreadcrumbs()
        this.$forceUpdate()
      })
    },
    fetchArtists () {
      console.log('fetch artists')
      var requestURL = `${this.$store.state.apiURL}/v2/artworks/${this.$route.params.id}/artists?ids=${this.artwork._artistIds.toString()}`
      this.axios.get(requestURL, this.$store.state.axiosConfig).then((response) => {
        if (!response.data.success) {
          this.$notify({
            group: 'main',
            title: 'Failed to get artist',
            text: 'Invalid request.'
          })
          return
        }

        var data = response.data.artists
        if (data === null) { return }

        this.artists = Vue._.clone(data, false)
        this.pageLoader = false
      })
    },
    fetchNearbyArtworks () {
      console.log('fetch nearby artworks', this.artwork.location.coordinates)
      var requestURL = `${this.$store.state.apiURL}/v2/artworks/${this.$route.params.id}/nearby?coords=${this.artwork.location.coordinates.toString()}&maxDistance=${25}`
      this.axios.get(requestURL, this.$store.state.axiosConfig).then((response) => {
        if (!response.data.success) {
          this.$notify({
            group: 'main',
            title: 'Failed to get artworks',
            text: 'Invalid request.'
          })
          return
        }

        var data = response.data.artworks
        if (data === null) { return }

        // let artworkCoords = this.artwork.location.coordinates
        this.nearbyArtworks = Vue._.clone(data, false).sort(function (a, b) {
          if (a._distance < b._distance) {
            return -1
          }
          if (a._distance > b._distance) {
            return 1
          }
          return 0
        })
      })
    },
    setupBreadcrumbs () {
      this.breadcrumbs = []
      this.breadcrumbs.push({ text: 'Artworks Map', disabled: false, to: '/#/artworks' })
      this.breadcrumbs.push({ text: (this.artwork.title ? this.artwork.title : this.artwork._id), disabled: false })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.vap-artworks-map{
  width: 100%;
  height: 500px;
}
.vap-admin-toolbar{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  background: rgba(0,0,0,.7);
  width: 100%;
}
.vap-artists-wrap{
  position: relative;
}
.vap-artists-content{
  position: absolute;
  top: 15%;
  width: 100%;
}
.vap-artists-wrap .display-3{
  text-align: center;
  font-size: 2em;
  text-shadow: 2px 2px #000;
}
.vap-artists-list{
  text-align: center;
}
.vap-artists-list li{
  border-radius: 100px;
  overflow: hidden;
  display: inline-block;
  text-align: center;
  margin: auto 10px;
  position: relative;
}
.vap-artists-list li .artist-image{
  width: 200px;
  height: 200px;
  display: block;
  background-size: cover;
  background-repeat: no-repeat;
}
.vap-artists-list li .detail-wrap{
  text-align: center;
  background: rgba(0,0,0,.6);
  color: #fff;
  position: absolute;
  z-index: 100000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  -webkit-transition: all .3s;
  transition: all .3s;
}
.vap-artists-list li:hover .detail-wrap{
  opacity: 1;
}

.bubble {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: table;
}

.bubble span {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}
.artwork-about {
  line-height: 1.5 !important;
}
</style>
<style>
.map-address{
  text-align: center;
  width: 100%;
  display: block;
}
.text-content{
  white-space: pre-line;
  font-size: 1.3em;
}
.text-content.empty p{
  background-color: rgb(210, 211, 213);
  height: 20px;
}
</style>


