<script>
import Vue from 'vue'
import VueGallery from 'vue-gallery'
import VueScrollTo from 'vue-scrollto'
export default {
  name: 'artistProfile',
  components: {
    'gallery': VueGallery
  },
  created () {
    this.initTabs()
    this.fetchArtist()
    VueScrollTo.scrollTo('#artist-page', 0, this.scrollOptions)
  },
  data () {
    return {
      msg: 'Welcome to Artist Profile',
      themeColor: (this.$store.state.config && this.$store.state.config.themeColor) || '#9E9E9E',
      enterpriseLogo: (typeof window.VividArts !== 'undefined' && window.VividArts.enterpriseLogo) || 'https://www.palmbeachculture.com/wp-content/uploads/cultural-council-palm-beach-county-logo-web@2x.png',
      enterpriseLogoContain: true,
      show: true,
      activeTab: 'tab-about',
      lightboxIndex: null,
      galleryLightboxIndex: null,
      scrollOptions: {
        el: '#artist-page',
        easing: 'ease-in',
        offset: -200,
        cancelable: true,
        onDone: function () {
          // scrolling is done
        },
        onCancel: function () {
          // scrolling has been interrupted
        },
        x: false,
        y: true
      },
      hasTabs: [false, false, false, false],
      breadcrumbs: [],

      artistArtworkImages: [],
      artistArtworks: [],
      enterprise: {},
      artist: {},
      enableEditArtistLink: false,
      placeHolderArtistImg: this.$store.state.placeholderArtistImg,

      mapCenter: { lat: 26.71348, lng: -80.0580457 },
      mapZoom: 15,
      artworkMarkers: [],
      bounds: null,
      infoContent: '',
      infoImage: '',
      infoWindowPos: {
        lat: 0,
        lng: 0
      },
      infoWinOpen: false,
      currentMidx: null,
      // optional: offset infowindow so it visually sits nicely on top of our marker
      infoOptions: {
        pixelOffset: {
          width: 0,
          height: -35
        }
      },
      tabs: [
        // {
        //   name: 'artworks',
        //   icon: 'brush'
        // },
        // {
        //   name: 'about',
        //   icon: 'info_outline'
        // }
        // {
        //   name: 'store',
        //   icon: 'store'
        // }
      ],
      items: [
        { active: true, title: 'Followers' },
        { active: true, title: 'Following' },
        { title: 'Artworks' }
      ],
      shareMenu: [
        { title: 'Facebook' },
        { title: 'Instagram' },
        { title: 'Twitter' },
        { title: 'Email' }
      ]
    }
  },
  watch: {
    '$route.params.id': function (id) {
      this.initTabs()
      this.fetchArtist()
      VueScrollTo.scrollTo('#artist-page', 0, this.scrollOptions)
    },
    activeTab (val) {
      if (['tab-gallery', 'tab-artworks'].indexOf(val) >= 0) {
        setTimeout(() => {
          this.$redrawVueMasonry()
        }, 1)
      }
    }
  },
  computed: {
    // artist () {
    //   var key = this.$route.params.id
    //   var a = this.artists.filter(function (item) {
    //     return item['.key'] === key
    //   })
    //   if (a.length > 0) {
    //     return a[0]
    //   }
    //   return null
    // },
    shouldIncludeCVTab () {
      console.log('shouldIncludeCVTab')
      var at = this.tabs.filter(function (item) {
        return item.name === 'cv'
      })

      if (typeof this.artist === 'undefined' || at.length > 0) {
        return false
      }

      if (typeof this.artist.exhibitions !== 'undefined' && this.artist.exhibitions.length) {
        this.addCVTab()
        return true
      }

      if (typeof this.artist.podcasts !== 'undefined' && this.artist.podcasts.length) {
        this.addCVTab()
        return true
      }

      if (typeof this.artist.videos !== 'undefined' && this.artist.videos.length) {
        this.addCVTab()
        return true
      }

      if (typeof this.artist.printBlogs !== 'undefined' && this.artist.printBlogs.length) {
        this.addCVTab()
        return true
      }

      return false
    }
    // enterprise () {
    //   var eid = window.VividArts.enterpriseId
    //   if (typeof eid !== 'undefined') {
    //     var e = this.enterprises.filter(function (item) {
    //       return item['.key'] === eid
    //     })
    //     if (e.length > 0) {
    //       return e[0]
    //     } else {
    //       return null
    //     }
    //   }
    //   return null
    // },
    // artistArtworks () {
    //   var key = this.$route.params.id
    //   var a = this.artworks.filter(function (item) {
    //     if (typeof item.artists !== 'undefined') {
    //       return item.status === 'active' && item.artists.indexOf(key) >= 0 && item.cloudinaryImageURL
    //     }
    //     return false
    //   })
    //   // if (a.length > 0) {
    //   //   var vm = this
    //   //   setTimeout(function () {
    //   //     vm.initMap()
    //   //   }, 1)
    //   // }
    //   return a
    // },
    // artistArtworkImages () {
    //   return this.artistArtworks.map(function (item) {
    //     return item.cloudinaryImageURL
    //   })
    // }
  },
  methods: {
    initMap () {
      console.log('init map')
      this.bounds = new window.google.maps.LatLngBounds()
      var bounds = this.bounds
      this.artworkMarkers = this.artistArtworks
        .filter(function (item) {
          return typeof item.location !== 'undefined' && typeof item.location.coordinates !== 'undefined' && item.location.coordinates.length === 2
        })
        .map(function (item) {
          bounds.extend({lat: parseFloat(item.location.coordinates[0]), lng: parseFloat(item.location.coordinates[1])})
          return {
            position: {lat: parseFloat(item.location.coordinates[0]), lng: parseFloat(item.location.coordinates[1])},
            icon: 'http://res.cloudinary.com/vividarts/image/upload/c_scale,w_30/v1506999888/poi-marker_hw5lin.png',
            infoText: item.name,
            infoImage: item.mainImageURL
          }
        })
      var fam = this.fitArtworkMarkers
      setTimeout(function () {
        fam()
      }, 500)
    },
    fitArtworkMarkers () {
      console.log('fitArtworkMarkers', this.$refs, this.bounds)
      if (typeof this.$refs.artworksMap !== 'undefined' && this.$refs.artworksMap.length) {
        this.$refs.artworksMap[0].$mapObject.fitBounds(this.bounds)
      }
    },
    fetchArtistArtworks () {
      console.log('fetch artist artworks')
      var url = this.$store.state.apiURL + `/v2/enterprise/${this.$store.getters.getEnterprise.enterpriseId}/artists/${this.artist._id}/artworks`
      // debugger

      this.axios.get(url, this.$store.state.axiosConfig).then((response) => {
        var data = response.data.artworks
        console.log('API | artists artworks', data)
        // debugger
        if (!data) { return }
        this.artistArtworks = data
        this.initTabs()
        this.initMap()
        // debugger
      })
    },
    fetchArtistRevisions () {
      console.log('fetch artists')
      var url = this.$store.state.apiURL + `/v2/artists/${this.$route.params.id}/revisions`
      // debugger

      this.axios.get(url, this.$store.state.axiosConfig).then((response) => {
        var data = response.data.revision
        console.log('API | artists revisons', data)
        // debugger
        if (!data) { return }
        var artistRevisions = Vue._.clone(data, false)

        this.artist = artistRevisions

        if (typeof this.artist.bio !== 'undefined') {
          this.artist.bio = unescape(this.artist.bio)
        }
        if (typeof this.artist.excerpt !== 'undefined') {
          this.artist.excerpt = unescape(this.artist.excerpt)
        }
        this.initTabs()
      })
    },
    toggleInfoWindow: function (marker, idx) {
      this.infoWindowPos = marker.position
      this.infoContent = marker.infoText
      this.infoImage = marker.infoImage

      // check if its the same marker that was selected if yes toggle
      if (this.currentMidx === idx) {
        this.infoWinOpen = !this.infoWinOpen
      } else {
        // if different marker set infowindow to open and reset current marker index
        this.infoWinOpen = true
        this.currentMidx = idx
      }
    },
    facebookURL () {
      return 'https://www.facebook.com/' + this.artist.facebook.replace('@', '')
    },
    instagramURL () {
      return 'https://www.instagram.com/' + this.artist.instagram.replace('@', '')
    },
    pinterestURL () {
      return 'https://www.pinterest.com/' + this.artist.pinterest
    },
    twitterURL () {
      return 'https://www.twitter.com/' + this.artist.twitter
    },
    websiteURL () {
      var url = ''
      if (this.artist.website.indexOf('http://') === -1 && this.artist.website.indexOf('https://') === -1) {
        url += 'http://'
      }
      url += this.artist.website
      return url
    },
    setBreadCrumbs () {
      this.breadcrumbs = []

      this.breadcrumbs.push(
        {
          text: 'All Artists',
          disabled: false,
          href: {name: 'ArtistsDirectory'}
        }
      )
      if (typeof this.artist === 'undefined' || typeof this.artist.permalink === 'undefined') {
        return
      }
      if (typeof this.artist.firstName !== 'undefined') {
        this.breadcrumbs.push(
          {
            text: this.artist.firstName ? `${this.artist.firstName} ${this.artist.lastName}` : this.artist.permalink,
            disabled: false
          }
        )
      } else {
        this.breadcrumbs.push(
          {
            text: `${this.artist.permalink}`,
            disabled: false
          }
        )
      }
    },
    fetchArtist () {
      console.log('fetch artists')
      if (!this.$store.getters.getEnterprise) {
        setTimeout(() => {
          this.fetchArtist()
        }, 300)
        return
      }
      if (typeof this.$route.query !== 'undefined' && typeof this.$route.query.preview !== 'undefined' && parseInt(this.$route.query.preview) === 1) {
        this.fetchArtistRevisions()
        return
      }
      var requestUrl = this.$store.state.apiURL + `/v2/enterprise/${this.$store.getters.getEnterprise.enterpriseId}/artists/${this.$route.params.id}`
      var vm = this
      this.axios.get(requestUrl, this.$store.state.axiosConfig).then((response) => {
        var data = response.data.artist
        // console.log('API | artists', data)
        if (data === null || typeof data === 'undefined') { return }

        vm.artist = Vue._.clone(data, false)
        if (typeof vm.artist.bio !== 'undefined') {
          vm.artist.bio = unescape(vm.artist.bio)
        }
        if (typeof vm.artist.bioHTML !== 'undefined') {
          vm.artist.bioHTML = unescape(vm.artist.bioHTML)
        }
        if (typeof vm.artist.excerpt !== 'undefined') {
          vm.artist.excerpt = unescape(vm.artist.excerpt)
        }
        vm.initTabs()
        vm.setBreadCrumbs()
        vm.fetchArtistArtworks()
        VueScrollTo.scrollTo('#artist-page', 0, this.scrollOptions)
        console.log('VueScrollTo')
      })
    },
    initTabs () {
      // console.log(this.artist.gallery)
      this.setBreadCrumbs()
      if (typeof this.artistArtworks !== 'undefined' && this.artistArtworks.length) {
        var hasItem1 = this.tabs.filter(function (item) {
          return item.name === 'artworks'
        })
        if (hasItem1.length === 0) {
          this.tabs.push({
            name: 'artworks',
            icon: 'brush'
          })
        }
      }

      // if (typeof this.artist.bio !== 'undefined' && this.artist.bio !== '') {
      var hasItem2 = this.tabs.filter(function (item) {
        return item.name === 'about'
      })
      if (hasItem2.length === 0) {
        this.tabs.push({
          name: 'about',
          icon: 'info_outline'
        })
        this.hasTabs[0] = true
      }
      // }

      if (typeof this.artist.gallery !== 'undefined' && this.artist.gallery.length) {
        var hasItem3 = this.tabs.filter(function (item) {
          return item.name === 'gallery'
        })
        if (hasItem3.length === 0) {
          this.tabs.push({
            name: 'gallery',
            icon: 'photo_library'
          })
          this.hasTabs[2] = true
        }
      }

      if (this.tabs.length > 0) {
        this.activeTab = 'tab-' + this.tabs[0].name
        console.log('activeTab -->', this.activeTab)
      }

      if (this.$store.getters.getUser !== null && (['admin', 'enterprise'].indexOf(this.$store.getters.getUser.type) >= 0 || (this.$store.getters.getUser.type === 'artist' && this.$store.getters.getUser.permalink === this.$route.params.id))) {
        this.enableEditArtistLink = true
      }
    },
    addCVTab () {
      this.tabs.push({
        name: 'cv',
        icon: 'stars'
      })
      this.hasTabs[3] = true
    }
  }
}
</script>

