<template>
  <v-container class="vivid-arts-directory">

    <pulse-loader v-if="!directory" class="va-page-loader" :color="color" :size="size" :margin="margin" :radius="radius"></pulse-loader>
    <v-layout row wrap>

      <v-flex class="page-header mb-5" xs12 v-if="enterpriseLogo">
        <v-container fluid grid-list-md>
          <v-layout row wrap>
            <v-flex xs12 sm4>
              <v-card-media
                class="enterprise-logo"
                :src="enterpriseLogo"
                :contain="enterpriseLogoContain"
                height="100px"
              ></v-card-media>
            </v-flex>
          </v-layout>
        </v-container>
      </v-flex>

      <v-flex xs12 v-if="directory && directory.enableHero && directory.gallery && directory.gallery.length">
        <swiper :options="swiperOption">
          <!-- slides -->
          <swiper-slide
            v-for="(item,i) in directory.gallery" v-bind:src="item.src" :key="i">
            <a v-if="item.url" :href="item.url">
              <div class="slider-img" :style="{backgroundImage: 'url('+item.src+')'}"></div>
              <div class="slider-caption-wrap" v-if="item.caption">
                <div class="caption-text">
                  {{item.caption}}
                </div>
              </div>
            </a>
            <div v-else>
              <div class="slider-img" :style="{backgroundImage: 'url('+item.src+')'}"></div>
              <div class="slider-caption-wrap" v-if="item.caption">
                <div class="caption-text">
                  {{item.caption}}
                </div>
              </div>
            </div>
          </swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
          <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
          <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
        </swiper>
      </v-flex>

      <!-- <v-flex xs12 v-if="enterprise && enterprise.artistHeroGallery && enterprise.artistHeroGallery.length">
        <v-carousel>
          <v-carousel-item
          v-for="(item,i) in enterprise.artistHeroGallery" v-bind:src="item" :key="i"></v-carousel-item>
        </v-carousel>
      </v-flex> -->

      <v-flex xs12 class="artist-search mt-5">
        <v-container fluid grid-list-md>
          <p class="headline">Filters</p>
          <v-layout row wrap>
            <v-flex xs12 sm12 md4 class="search-textfield-wrap">
              <v-text-field
                name="input-1-3"
                v-model="artistSearch"
                class="search-textfield"
                label="Search Artist"
                single-line
                prepend-icon="search"
              ></v-text-field>
              <a v-show="artistSearch" href class="clear-search btn" @click.prevent="artistSearch = ''"><v-icon>fa-times-circle</v-icon></a>
            </v-flex>
            <v-flex xs12 sm12 md4>
              <span v-show="false">{{viewableArtists.length}}/{{fRegions.length}}</span>
              <p>Region</p>
              <v-select v-bind:items="cities" v-model="dd1" overflow label="Select" class="capitalize"></v-select>
            </v-flex>
            <v-flex xs12 sm12 md4>
              <p>Type</p>
              <v-select v-bind:items="activeCategories" v-model="dd2" overflow item-value="name" item-text="name" label="Select"></v-select>
            </v-flex>
            <!-- <v-flex sm12 sm4 v-if="false">
              <p>Sub-Type</p>
              <v-select v-bind:items="activeCategories" v-model="dd3" overflow item-value="name" item-text="name" label="Select"></v-select>
            </v-flex> -->
          </v-layout>
        </v-container>
      </v-flex>

      <v-flex xs12 id="artist-grid-wrap" ref="artistGridWrap">
        <v-container fluid v-bind="{ [`grid-list-md`]: true }">
          <v-layout row wrap>
            <v-flex
              xs12 sm6
              :class="itemClassSize"
              :key="artist.permalink"
              v-for="artist in viewableArtists"
              v-if="viewableArtists.length > 0"
              class="pa-4">
              <v-card flat tile>
                <router-link :to="{ name: 'ArtistProfile', params: { id: artist.permalink} }">
                  <v-card-media class="profile-image"
                    :src="artist.profileURL | thumbnailURL"
                    height="200px"
                  >
                  </v-card-media>
                </router-link>
                <v-card-title primary-title>
                  <div>
                    <router-link class="artist-name" :to="{ name: 'ArtistProfile', params: { id: artist.permalink} }">
                      <h3 class="headline mb-0" v-if="artist.firstName || artist.lastName">{{ artist.firstName }} {{ artist.lastName }}</h3>
                    </router-link>
                    <div class="artist-expcert">{{ artist.excerpt | truncate(150) }}</div>
                  </div>
                </v-card-title>
                <v-card-actions>
                  <router-link class="indigo--text btn btn--flat ml-3" tag="button" :to="{ name: 'ArtistProfile', params: { id: artist.permalink} }">View Profile</router-link>
                  <!-- <router-link class="indigo--text btn btn--flat" tag="button" :to="{ name: 'EditArtist', params: { id: artist.permalink} }">Edit</router-link> -->
                </v-card-actions>
              </v-card>
            </v-flex>
            <v-flex v-if="viewableArtists.length === 0" xs12>
              <h2 class="headline text-xs-center">{{errorMsg}}</h2>
            </v-flex>
            <v-flex xs12 class="text-xs-center" v-if="viewableArtists.length">
              <v-pagination :length="numberOfPages" v-model="page" circle></v-pagination>
            </v-flex>
          </v-layout>
        </v-container>

      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import Vue from 'vue'
import VueScrollTo from 'vue-scrollto'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'

export default {
  name: 'Directory',
  components: {
    swiper,
    swiperSlide,
    PulseLoader
  },
  created () {
    this.fetchArtists()
    this.fetchEnterpriseDirectory()
  },
  data () {
    return {
      enterpriseLogo: window.VividArts.enterpriseLogo,
      enterpriseLogoContain: true,
      artistSearch: null,

      pageLoder: true,
      radius: 40,
      color: '#136FAB',
      size: '30px',

      scrollOptions: {
        el: '#artist-grid-wrap',
        easing: 'ease-in',
        offset: -200,
        cancelable: true,
        onDone: function () {
          // scrolling is done
        },
        onCancel: function () {
          // scrolling has been interrupted
        },
        x: false,
        y: true
      },
      page: 1,
      maxArtistCount: 6,
      numberOfPages: 0,
      itemClassSize: 'md4',
      dd1: null,
      dd2: null,
      dd3: null,
      artistTypes: window.VividArts.types || [],
      cities: [],
      // enterprise: {},
      fRegions: [],

      activeCategories: [],
      directories: {},
      directory: {},
      directoryType: 'artists',
      errorMsg: 'No results found.',

      swiperOption: {
        pagination: '.swiper-pagination',
        nextButton: '.swiper-button-next',
        prevButton: '.swiper-button-prev',
        paginationClickable: true,
        spaceBetween: 30,
        loop: true,
        autoplay: 2500,
        effect: 'fade'
      },

      heroCarousel: [],

      originalActiveArtists: [],
      filteredArtists: [],
      viewableArtists: [],

      dropdown_regions: [],
      dropdown_icon: ['All', 'list', 'favorite', 'delete'],
      dropdown_edit: [
        { text: 'All' },
        { text: '100%' },
        { text: '75%' },
        { text: '50%' },
        { text: '25%' },
        { text: '0%' }
      ]
    }
  },
  watch: {
    dd1 (newVal, oldVal) {
      if (newVal !== oldVal) {
        this.page = 1
        this.applyArtistFilters()
        this.filterArtistsByPage(this.filteredArtists)
      }
    },
    enterprise (newVal, oldVal) {
      if (newVal !== oldVal) {
        this.fetchArtists()
        this.fetchEnterpriseDirectory()
      }
    },
    dd2 (newVal, oldVal) {
      if (newVal !== oldVal) {
        this.page = 1
        this.applyArtistFilters()
        this.filterArtistsByPage(this.filteredArtists)
      }
    },
    page (newVal, oldVal) {
      if (newVal !== oldVal) {
        VueScrollTo.scrollTo('#artist-grid-wrap', 500, this.scrollOptions)
        this.filterArtistsByPage(this.filteredArtists)
      }
    },
    originalActiveArtists (newVal, oldVal) {
      if (typeof newVal !== 'undefined' && newVal.length > 0 && newVal !== oldVal) {
        this.applyArtistFilters()
        this.filterArtistsByPage(newVal)
      }
    },
    artistSearch (newVal, oldVal) {
      if (newVal !== oldVal) {
        if (newVal === '') {
          this.filterArtistsByPage(this.filteredArtists)
          return
        }
        console.log('artistSearch', newVal)
        var array = this.artistSearchHandler(newVal.toLowerCase(), this.originalActiveArtists, this.filteredArtists)
        if (typeof array !== 'undefined') {
          this.filterArtistsByPage(array)
          // console.log('artistSearch', newVal, array)
        }
      }
    }
  },
  computed: {
    enterprise () {
      return this.$store.getters.getEnterprise
    }
  },
  methods: {
    sortArtistsByName (a, b) {
      if (a.fullName < b.fullName) {
        return -1
      }
      if (a.fullName > b.fullName) {
        return 1
      }
      return 0
    },
    filterArtistsByPage (artists) {
      console.log('filterArtistsByPage')
      let mac = this.maxArtistCount
      let al = artists.length
      let start = (this.page - 1) * mac
      this.numberOfPages = Math.ceil(al / mac)
      let rtn = Vue._.clone(artists, true).splice(start, mac)
      // console.log('numberOfPages', al, mac, this.numberOfPages, rtn)
      this.viewableArtists = rtn
    },
    applyArtistFilters () {
      let dd1Val = this.dd1 || 'All'
      let dd2Val = this.dd2 || 'All'
      // console.log('filtering artist by region', dd1Val)
      // console.log('filtering artist by category', dd2Val)
      this.filteredArtists = this.originalActiveArtists
      .filter(function (item) {
        // console.log(item.regionId, dd1Val)
        if (dd1Val === 'All') {
          return true
        } else if (typeof item.city !== 'undefined' && item.city.toLowerCase().indexOf(dd1Val.toLowerCase()) >= 0) {
          return true
        }
        return false
      })
      .filter(function (item) {
        // console.log(item.fullName, item, dd2Val)
        if (dd2Val === 'All') {
          return true
        } else if (typeof item.styles !== 'undefined') {
          if (typeof item.styles !== 'undefined' && Array.isArray(item.styles) && item.styles.indexOf(dd2Val) >= 0) {
            return true
          }
        }
        return false
      })
      .sort(this.sortArtistsByName)
    },
    artistSearchHandler (str, arr, arr2) {
      // var str = this.artistSearch
      // console.log('Searching for ', str, arr, arr2)
      if (typeof arr === 'undefined') {
        return
      }
      return arr.filter(function (item) {
        // console.log(item.fullName.toLowerCase(), str, (item.fullName.toLowerCase().indexOf(str) >= 0))
        if (item.fullName && item.fullName.toLowerCase().indexOf(str) >= 0) {
          return true
        }
        if (item.firstName && item.firstName.toLowerCase().indexOf(str) >= 0) {
          return true
        }
        if (item.lastName && item.lastName.toLowerCase().indexOf(str) >= 0) {
          return true
        }
        if (item.email && item.email.toLowerCase().indexOf(str) >= 0) {
          return true
        }
        if (item.permalink && item.permalink.toLowerCase().indexOf(str) >= 0) {
          return true
        }
        if (item.city && item.city.toLowerCase().indexOf(str) >= 0) {
          return true
        }
      })
    },
    heroClickHandler (item) {
      if (typeof item.url !== 'undefined') {
        console.log('heroClickHandler', item.url)
      }
    },
    fetchArtists () {
      // console.log('fetch artists')
      if (this.enterprise === null || typeof this.enterprise.enterpriseId === 'undefined') {
        return
      }
      var url = this.$store.state.apiURL + `/v2/artists/enterprise/${this.enterprise.enterpriseId}`
      this.axios.get(url, this.$store.state.axiosConfig).then((response) => {
        // var keys = Object.keys(response.data.artists)
        var data = Object.values(response.data.artists)
        .map(function (item, idx) {
          // item.permalink = keys[idx]
          return item
        })
        // console.log('API | artists', data)

        var placeholderArtistImg = this.$store.state.placeholderArtistImg
        var artists = data.filter(function (artist) {
          return artist.status === 'active' && (artist.enableDirectoryListing === true)
        })
        .map(function (item) {
          if (typeof item.bio !== 'undefined') {
            item.bio = unescape(item.bio)
          }
          if (typeof item.fullName !== 'undefined') {
            item.fullName = unescape(item.fullName)
          }
          if (typeof item.excerpt !== 'undefined') {
            item.excerpt = unescape(item.excerpt)
          }
          if (typeof item.profileURL === 'undefined' || item.profileURL === '') {
            item.profileURL = placeholderArtistImg
          }
          return item
        })
        .sort(this.sortArtistsByName)

        var titleCase = this.titleCase
        this.cities = Array.from(new Set(artists.map(function (item) {
          return (typeof item.city !== 'undefined') ? titleCase(item.city.toLowerCase()) : ''
        })))
        .sort()
        console.log('Artists cities', this.cities)

        this.cities.unshift('All')

        // console.log('setting artists', artists)
        // this.viewableArtists = Vue._.clone(artists, false)
        this.originalActiveArtists = artists
        // this.filterItemsByStatus(this.status)
      })
    },
    fetchEnterpriseDirectory () {
      // console.log('fetch enterprise directory', this.$store.state, this.$store.state.apiURL)
      if (this.enterprise === null || typeof this.enterprise.enterpriseId === 'undefined') {
        return
      }
      var url = this.$store.state.apiURL + `/v2/enterprise/${this.enterprise.enterpriseId}/directory/artists`
      // var payload = { }
      var vm = this
      this.axios.get(url, this.$store.state.axiosConfig).then((response) => {
        var data = response.data.directory[0]
        console.log('fetchEnterpriseDirectory', data)

        if (data === null) { return }

        vm.directory = data
        // console.log('directoryType ->')
        if (vm.directoryType === 'artists') {
          vm.activeCategories = vm.directory.categories
          if (typeof vm.activeCategories !== 'undefined' && vm.activeCategories.length > 0) {
            vm.activeCategories.unshift({name: 'All'})
          } else {
            vm.activeCategories = [{name: 'All'}]
          }
        }
        if (typeof vm.directory.loop !== 'undefined') {
          vm.swiperOption.loop = vm.directory.loop
        }
        if (typeof vm.directory.autoplay !== 'undefined') {
          vm.swiperOption.autoplay = vm.directory.autoplay
        }
        if (typeof vm.directory.effect !== 'undefined') {
          vm.swiperOption.effect = vm.directory.effect
        }
        if (typeof vm.directory.direction !== 'undefined') {
          vm.swiperOption.direction = vm.directory.direction
        }
        if (typeof vm.directory.itemsPerPage !== 'undefined') {
          vm.maxArtistCount = vm.directory.itemsPerPage
        }

        if (typeof vm.directory.itemsPerRow !== 'undefined') {
          switch (parseInt(vm.directory.itemsPerRow)) {
            case 1:
              vm.itemClassSize = 'md12'
              break
            case 2:
              vm.itemClassSize = 'md6'
              break
            case 3:
              vm.itemClassSize = 'md4'
              break
            case 4:
              vm.itemClassSize = 'md3'
              break
            default:
              vm.itemClassSize = 'md4'
          }
        }
        // console.log('active directory', vm.directory)
        // this.form = this.initFormWithData(this.artist)
      })
    },
    titleCase (str) {
      var splitStr = str.toLowerCase().split(' ')
      for (var i = 0; i < splitStr.length; i++) {
        splitStr[i] = splitStr[i].charAt(0).toUpperCase() + splitStr[i].substring(1)
      }
      return splitStr.join(' ')
    }
  }
}
</script>

